/*===== VARIABLES CSS =====*/
:root {
  --nav-height: 100px;
  --footer-height: 200px;
  --header-height: 4rem;
  --font-semi: 600;
}

/*===== Colores =====*/
:root {
  /* --first-color: #4070f4; */
  --first-color: #732929;
  --second-color: #595959;

  --nav-background-color: #f2f2f2;
  --nav-hover-color: #732929;
  --nav-text-color: #595959;
  --background-color: var(--primary-color);
}

:root {
  --body-font: "Poppins", sans-serif;

  --nav-title-font-size: 1.5rem;
  --nav-link-font-size: 1rem;

  --home-title-font-size: 2rem;
  --home-image-width: 200px;

  --skill-title-font-size: 1.25rem;
  --skill-subtitle-font-size: 1.1em;

  --big-font-size: 3.5rem;
  --h2-font-size: 1.25rem;
  --normal-font-size: 0.938rem;

  --margin-x-skill: 5px;
}

/* iPad screen and larger */
@media screen and (min-width: 768px) {
  :root {
    --home-title-font-size: 2.5rem;
    --home-image-width: 200px;
  }
}

@media screen and (min-width: 1024px) {
  :root {
    --home-title-font-size: 3.5rem;
    --home-image-width: 250px;
  }
}

/* extra big screen */
@media screen and (min-width: 1200px) {
  :root {
    --home-title-font-size: 4rem;
    --home-image-width: 400px;
    --home-margin-top: 15rem;
  }
}

/*===== Margenes =====*/
:root {
  --mb-1: 0.5rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  --mb-6: 3rem;
}

/*===== z index =====*/
:root {
  --z-back: -10;
  --z-normal: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
}

/*===== BASE =====*/
*,
::before,
::after {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
