/* iPad screen and larger */
@media screen and (min-width: 800px) {
  .Header_NavToggleLabel__weL-Q {
    display: none;
  }

  .Header_NavContainer__1zoTy {
    display: grid;
    grid-template-columns: 1fr auto minmax(400px, 3fr) 1fr;
  }

  .Header_NavLogo__26cw8 {
    grid-column: 2 / 3;
  }

  /* 这里有一个小瑕疵，当打开toggle的时候，再缩小，这个display: block 会继承过去 */
  .Header_NavToggle__24Kp0:checked ~ .Header_NavItems__12lyY {
    display: -webkit-flex;
    display: flex;
  }

  .Header_NavItems__12lyY {
    all: unset;
    grid-column: 3 / 4;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    list-style: none;
  }

  .Header_NavItem__25d6V {
    margin-bottom: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
  }

  .Header_NavItem__25d6V a {
    position: relative;
  }

  /* animated outline */
  .Header_NavItem__25d6V a::before {
    content: "";
    display: block;
    height: 5px;
    background: var(--nav-hover-color);
    position: absolute;
    top: -0.75em;
    left: 0;
    right: 0;
    -webkit-transform: scale(0, 1);
            transform: scale(0, 1);
    -webkit-transform-origin: right;
            transform-origin: right;
    transition: -webkit-transform ease-in-out 250ms;
    transition: transform ease-in-out 250ms;
    transition: transform ease-in-out 250ms, -webkit-transform ease-in-out 250ms;
  }

  .Header_NavItem__25d6V a:hover::before {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }

  .Header_NavItem__25d6V a.Header_Active__4TDzY::before {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
    color: var(--nav-hover-color);
  }
}

